body {
  box-sizing: border-box;
  --primary--color: #0e1116;
  --secondary--color: #029d5b;
  --text--color: white;
  --shadow: 5px 2px 2px #fff3, -1px -1px 2px #fff3;
  margin: 0;
  padding: 0;
  font-family: poppins;
  font-weight: bold;
  transition: all 1s;
  display: flex;
}

.mew {
  margin-top: 46%;
}

main {
  width: 100vw;
  height: 100vh;
  flex-direction: column;
  flex: auto;
  transition: all .1s;
  display: flex;
}

.main--header {
  width: 20vw;
  width: max-content;
  grid-gap: .2em;
  transition: all 1s;
  -webkit-transition: all .5s;
  -o-transition: all 1s;
  background-color: #fff;
  flex: auto;
  grid-template-columns: 1fr 3fr;
  margin-right: 1em;
  -moz-transition: all 1s;
  -ms-transition: all 1s;
  display: grid;
}

.sticky--caller {
  width: 100vw;
  z-index: 1;
  box-shadow: var(--shadow);
  padding: 0;
  position: fixed;
  top: 0;
  left: 0;
}

.static--responder {
  z-index: 1;
  background: var(--text--color);
  color: #5c0a0a;
  margin: 9vh 0 0;
  position: fixed;
  top: 0;
  left: 0;
}

.hide-nav {
  width: 0;
  height: 0;
  opacity: 0;
  transform-origin: 100% 100%;
  background-color: #080447;
  margin: 0;
  transform: translate(-20vw)rotate(-95deg);
}

.nav--img {
  width: 30px;
  height: 30px;
  margin: 1em;
}

.main {
  background: var(--primary--color);
  flex-direction: column;
  display: flex;
}

.optional {
  flex-direction: column;
  margin-top: 1em;
  margin-right: 3vw;
  display: flex;
}

.optional a {
  color: var(--text--color);
  -o-transition: all .1s;
  color: var(--secondary--color);
  border-top-left-radius: 1em;
  border-bottom-left-radius: 1em;
  padding-left: .5em;
  text-decoration: none;
  transition: all .1s;
}

.optional a:hover {
  background-color: var(--text--color);
  height: 2em;
  color: var(--primary--color);
  transform: translate(-.8em);
}

.vulnerable {
  margin-top: 3em;
}

.btn--search {
  background-color: var(--secondary--color);
}

.btn--search:hover {
  box-shadow: -.5px -.6px 3px #ffffffe6;
}

.icon--search {
  width: 10px;
}

.input--text {
  width: 20vw;
  transition: all 1s;
}

.input--text:focus {
  width: 40vw;
}

.head--head--head {
  background: var(--primary--color);
  display: flex;
}

.jubtube--logo {
  width: 6vw;
  height: 6vh;
  margin-top: 2vh;
  margin-right: 5vw;
}

#content--space {
  background: var(--primary--color);
  height: 100vh;
  margin-top: 3em;
}

.reject--icon {
  width: 30px;
  height: 30px;
  animation: 4s ease-out infinite stud;
}

.nav--img {
  animation: 4s ease-out infinite stud;
}

.reject {
  cursor: pointer;
  transform: translate(.8em, 1em);
}

@keyframes stud {
  100% {
    transform: scale(1.4);
    -webkit-transform: scale(1.2);
    -moz-transform: scale(1.4);
    -ms-transform: scale(1.4);
    -o-transform: scale(1.4);
  }
}

.secondary--header {
  margin-top: 1em;
}

@media (max-width: 630px) {
  body {
    flex-direction: column;
  }

  .mew {
    margin-top: 0;
  }

  .nav--img {
    width: 20px;
    height: 20px;
  }

  .main--header {
    grid-template-rows: max-content;
    grid-template-columns: 100vw;
  }

  .main {
    flex-direction: row;
    justify-content: center;
  }

  .optional {
    height: 7vh;
    flex-direction: row;
    justify-content: center;
    margin: 1px;
  }

  .optional a:hover {
    color: var(--primary--color);
    background-color: #0000;
    font-weight: 300;
    transform: translate(0);
  }

  .reject {
    position: relative;
    top: -1vh;
    left: -30vw;
  }
}

@media (max-width: 430px) {
  .reject {
    position: relative;
    top: -1vh;
    left: -40vw;
  }

  .reject--icon {
    width: 20px;
    height: 20px;
  }
}

@media (max-width: 330px) {
  .reject {
    position: relative;
    top: -4.3vh;
    left: -50vw;
  }

  .reject--icon {
    width: 20px;
    height: 20px;
  }

  .optional a {
    font-size: .8em;
  }
}

.reject--icon:hover {
  filter: sepia(230);
  -webkit-filter: sepia(230);
}

